@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
input {
  padding: 10px;
  font-size: 18px;
  font-family: Arial, Helvetica, sans-serif;
  border: none;
  border-radius: 5px;
}
button {
  padding: 10px;
  min-width: 150px;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  background-color: rgb(85, 135, 228);
  color: #eee;
}

button[type=submit] {
  background-color: rgb(11, 194, 163);
}

.remove {
  background-color: rgb(223, 18, 59);
}

.blocked {
  background-color: #454555;
  cursor: not-allowed;
}

.blocked:hover {
  transform: none !important;
}
#header {
  background-color: #252530;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 100px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
}

#title {
  color: #eee;
}

#nav {
  display: flex;
  border: 2px solid rgb(165, 160, 160);
  border-radius: 5px;
  grid-gap: 10px;
  gap: 10px;
  padding: 5px;
}
#footer {
  background-color: #252530;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
  padding: 10px 0;
}

#footer a {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  padding: 2px;
}

#footer-text {
  font-size: 18px;
  color: #fff;
}
.section {
  width: 70vw;
  background-color: rgb(240, 239, 239);
  margin: 0 auto;
  padding: 30px 0;
  border-radius: 5px;
}

.section form {
  width: 30vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
}

.section .info {
  margin: 0 auto;
  width: 50vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.info .data {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  font-size: 20px;
}

.start-finish {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}

.start-finish input {
  width: 14.6vw !important;
}

.submit-remove {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}

.submit-remove button {
  width: 14.6vw !important;
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  font-family: 'Roboto', Arial, sans-serif;
}

button {
  cursor: pointer;
  transition: all .2s;
}

button:hover {
  transform: scale(1.05);
}

a {
  text-decoration: none;
  color: #222;
}

html, body, #root {
  height: 100%;
}

#edit {
  margin: 100px 0 25px 0;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

#preview {
  min-height: 92.3vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(240, 239, 239);
  width: 60vw;
  margin: 0 auto;
}

#preview h2 {
  color: #222;
  font-size: 30px;
}

#preview #content {
  width: 40vw;
  margin: 0 auto;
}

#content h3 {
  font-size: 26px;
  color: rgb(85, 135, 228);
  border-top: 2px solid rgb(85, 135, 228);
  border-bottom: 2px solid rgb(85, 135, 228);
  text-indent: 15px;
  margin-bottom: 10px;
}

#contacts {
  color: #585858;
  margin: 10px 0 20px 0;
  font-size: 20px;
}

#education p, #practical p {
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
}

div span p {
  font-size: 16px !important;
  border-right: 2px solid rgb(85, 135, 228);
  padding-right: 10px;
  margin-right: 10px;
}

#practical {
  margin-top: 20px;
}

/* custom scroll */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #c7c7c7; 
  background: #e7e6e6;
}
 
::-webkit-scrollbar-thumb {
  background: #252535;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #353545; 
}
